.Flight-info {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	padding: 10px;
	margin: 10px;
}
@media screen and(min-width: 1100px) {
	.Flight-info {
		img {
			margin-right: 4%;
		}
		.detail-label {
			&:nth-child(2) {
				flex: 1 1 9%;
				text-align: left;
			}
			&:nth-child(3) {
				flex: 1 1 9%;
				text-align: left;
			}
			&:nth-child(4) {
				flex: 1 1 9%;
				text-align: left;
			}
			&:nth-child(5) {
				flex: 1 1 9%;
				text-align: left;
			}
			&:nth-child(6) {
				flex: 1 1 9%;
				text-align: left;
			}
		}
		Button {
			margin-left: 4%;
			flex: 1 1 10%;
		}
	}
}
@media screen and(min-width: 601px) and(max-width: 1099px) {
	.Flight-info {
		.detail-label {
			&:nth-child(2) {
				flex: 5 1 80%;
				text-align: end;
				margin-bottom: 15px;
			}
			&:nth-child(3) {
				flex: 2 1 20%;
			}
			&:nth-child(4) {
				flex: 2 1 20%;
			}
			&:nth-child(5) {
				flex: 2 1 20%;
			}
			&:nth-child(6) {
				flex: 2 1 20%;
			}
		}
		Button {
			flex: 1 1 70%;
		}
	}
}
@media screen and(min-width: 401px) and(max-width: 600px) {
	.Flight-info {
		.detail-label {
			&:nth-child(2) {
				flex: 5 1 80%;
				text-align: end;
				margin-bottom: 15px;
			}
			&:nth-child(3) {
				flex: 2 1 33%;
			}
			&:nth-child(4) {
				flex: 2 1 33%;
			}
			&:nth-child(5) {
				flex: 2 1 11%;
				text-align: end;
			}
			&:nth-child(6) {
				flex: 2 1 33%;
			}
			&:nth-child(7) {
				flex: 2 1 33%;
			}
		}
		Button {
			flex: 2 1 15%;
		}
	}
}
@media screen and(min-width: 231px) and(max-width: 400px) {
	.Flight-info {
		.detail-label {
			&:nth-child(2) {
				flex: 5 1 80%;
				text-align: end;
				margin-bottom: 15px;
			}
			&:nth-child(3) {
				flex: 1 1 50%;
				text-align: left;
			}
			&:nth-child(4) {
				flex: 1 1 50%;
				text-align: end;
			}
			&:nth-child(5) {
				flex: 1 1 50%;
				text-align: left;
			}
			&:nth-child(6) {
				flex: 1 1 50%;
				text-align: end;
			}
			&:nth-child(7) {
				flex: 1 1 50%;
				text-align: left;
			}
		}
		Button {
			flex: 1 1 50%;
		}
	}
}
@media screen and(max-width: 230px) {
	.Flight-info {
		.detail-label {
			&:nth-child(2) {
				flex: 1 1 100%;
				text-align: left;
				margin-bottom: 10px;
			}
			&:nth-child(3) {
				flex: 1 1 100%;
				text-align: left;
				margin-bottom: 10px;
			}
			&:nth-child(6) {
				flex: 1 1 100%;
				text-align: left;
				margin-bottom: 10px;
			}
			&:nth-child(7) {
				flex: 1 1 100%;
				text-align: left;
				margin-bottom: 10px;
			}
			&:nth-child(4) .Flight-info .detail-label:nth-child(5) {
				flex: 1 1 100%;
				text-align: left;
				margin-bottom: 10px;
			}
		}
		Button {
			flex: 1 1 80%;
		}
	}
}
